interface IWord {
  name?: string;
  en?: string[];
  cs?: string[];
  audio?: number[];
  words?: IWord[];
  uuid?: string;
  parent?: IWord;
}

function tree(array: IWord[], parent?: IWord) {
  let rows: any[] = [];

  array.forEach((node, index) => {
    if (typeof node.name !== "undefined") {
      node.uuid = v1();
      node.parent = parent;
    }

    if (typeof node.words !== "undefined") {
      rows.push(...tree(node.words, node));
    }
  });

  return rows;
}

tree(words);
